<template>
  <el-container class="login-image">
    <div class="Main">
      <div class="main">
        <div class="login-title">南泥湾采油厂智慧党建平台</div>
        <div class="login-main">
          <div class="login-form">
            <el-form :model="loginInfo" class="login-info">
              <el-form-item>
                <div class="userName">
                  <img src="~@/assets/login/yhm.png" alt />
                </div>
                <el-input
                  v-model="loginInfo.loginName"
                  ref="loginName"
                  type="text"
                  placeholder="请输入账号"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <div class="password">
                  <img src="~@/assets/login/mm.png" alt />
                </div>
                <el-input
                  v-model="loginInfo.password"
                  ref="password"
                  type="password"
                  placeholder="请输入密码"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button @click="loginByLoginEvt" class="inpt-btn">登录</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </div>
      <div class="bottom">
        版权所有：南泥湾******&nbsp;&nbsp;&nbsp;备案号：
        <a
          href="http://www.beian.miit.gov.cn"
          target="_blank"
        >豫ICP备00000000号-1</a>&nbsp;&nbsp;&nbsp;技术支持：河南云信海网络技术有限公司
      </div>
    </div>
  </el-container>
</template>

<script>
import axios from "axios";
import config from "@/config/index";
import { login } from "@/http/AppApi";
import { clearLocalStrorage, setToken } from "@/utils/LocalDataUtil";
import CookieUtil from "@/utils/CookieUtil";

export default {
  data() {
    return {
      login: "登录",
      loginInfo: {
        loginName: "",
        password: "",
      },
      publicKey: null,
      primaryPwd: "",
    };
  },
  mounted() {
    this.getErrorCode();
    this.$store.state.signal = "";
  },
  components: {},
  methods: {
    loginByLoginEvt() {
      if (this.loginInfo.loginName == "") {
        this.$message.warning("请输入账号!");
        this.$refs.loginName.focus();
        return;
      }
      if (this.loginInfo.password == "") {
        this.$message.warning("请输入密码!");
        this.$refs.password.focus();
        return;
      }
      this.loginFun();
    },
    loginFun() {
      login(this.loginInfo).then((resp) => {
        if (resp && resp.code === "0000") {
          let data = resp.data;
          setToken(data.token);
          this.$router.push({ path: "/" });
        }
      });
    },
    getErrorCode() {
      let error = this.$route.params.error;
      if (error) {
        if (error === "Unauthorized") {
          this.$message({ message: "登录失效，请重新登录!", type: "error" });
        } else if (error === "lougout") {
          this.$message({ message: "退出成功", type: "success" });
        }
      }
      CookieUtil.clearToken();
      clearLocalStrorage();
    },
  },
};
</script>
<style scoped>
.login-image {
  background-image: url(../assets/login/login_bg.png);
  background-size: 100% 100%;
}
.Main {
  width: 100%;
  height: 100%;
}
.main {
  width: 100%;
  height: 100%;
  margin-top: 10%;
}
.el-form-item {
  margin-bottom: 20px;
  text-align: center;
}
.el-form >>> .el-input__inner {
  background-color: rgba(255, 255, 255, 1);
  padding: 0 20px 0px 35px;
  color: #919191;
}
.bottom {
  position: absolute;
  bottom: 4%;
  width: 100%;
  text-align: center;
  color: white;
  font-size: 14px;
}
.bottom a {
  text-decoration: none;
  color: white;
}
.bottom a:hover {
  color: #333333;
}
.userName,
.password,
.verificationCode {
  width: 28px;
  height: 28px;
  position: absolute;
  z-index: 1;
  top: 15%;
  left: 2%;
}
.login-title {
  margin-bottom: 70px;
  line-height: 100%;
  font-size: 50px;
  color: #fff;
  text-align: center;
  font-family: tonjay;
}
.login-main {
  width: 500px;
  height: 250px;
  padding: 0 45px;
  position: relative;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0px 0px 8px 0px rgba(246, 18, 45, 0.09);
  border-radius: 3px;
  margin: 0px auto;
  box-sizing: border-box;
}
.login-info {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.login-form {
  position: relative;
  height: 100%;
  width: 100%;
}
.inpt-btn {
  width: 100% !important;
  background: rgba(239, 166, 37, 1);
  box-shadow: 0px 5px 13px 0px rgba(47, 32, 6, 0.28);
  border-radius: 4px;
  color: #fff;
}
.inpt-btn:hover {
  background: rgba(239, 166, 37, 0.8);
  color: #fff;
}
.inpt-btn:focus,
.inpt-btn:focus:active,
.inpt-btn:visited {
  background: rgba(239, 166, 37, 1);
  color: #fff;
}
@font-face {
  /* font-test*/
  font-family: tonjay;
  src: url("../assets/font/fzzyt.ttf");
}
</style>



